<template>
  <div class="stock-card">
    <div class="card-inner col no-gutters d-flex full-center"><img :src="getImage" :alt="title" />
      <h6><a class="accent-link">{{title}}</a></h6>
      <article>{{description}}</article>
      <input class="small" type="button" value="Подробнее" />
    </div>
  </div>
</template>

<script>
export default {
  props: ['img', 'title', 'description'],
  computed: {
    getImage() {
      if (!this.img) return

      return require(`../../assets/img/promotions/${this.img}.jpg`)
    }
  },
}
</script>
